import {useNavigate} from "react-router-dom";
import {PatientRoutingPrefix} from "../Routing/PatientRouting";
import React, {useState} from "react";
import {SearchIcon} from "../../../Common/Icon";
import {useTranslation} from "react-i18next";

export default function PatientSearchBox() {
    const navigate = useNavigate();
    const [searchValue,setSearchValue] = useState("")
    const {t} = useTranslation();
    
    const goToSearchList = () => {
        navigate({
            pathname: "/" + PatientRoutingPrefix,
            search: searchValue.length > 0 ? "search="+searchValue : "",
        })
        setSearchValue("")
    }
    return (
        <div className="flex-column form-control ">
            <div className="flex-row field patient-search">
                <input
                    placeholder={t("Zoek een patient")}
                    onChange={(ev)=>{setSearchValue(ev.target.value)}}
                    onKeyDown={(ev) => {
                        if (ev.key === "Enter") {
                            goToSearchList()
                        }
                    }}
                    value={searchValue}
                    className="flex-grow-1"
                    type="text"
                />
                {searchValue.length > 3 && <div className={"unit"}><SearchIcon onClick={() => goToSearchList(searchValue)}small /></div>}
            </div>
        </div>
    )
}
