export class Patient {
    
    id: string
    firstname: string
    lastname: string    
    sexe: string
    ssn: string
}


export function formatSSN(ssn: string): string {
    // TODO length checks?
    return ssn.substring(0, 2) + "."+ssn.substring(2, 4) + "."+ ssn.substring(4,6) + 
        "-" + ssn.substring(6,9) + "." + ssn.substring(9,11)
}