import './overlay.scss';
import React from "react";
import {useOverlay} from "./OverlayProvider";
import {CloseIcon} from "../Icon";


const Overlay = () => {
    const {config, clear} = useOverlay()
    if (!config) {
        return <></>
    }

    return (
			<div onClick={(ev) => {clear()}} className={`${config.className ?? ""} overlay-layer`}>
				<div className="overlay-body"  onClick={ev => ev.stopPropagation()}>
                    {config.showCloseButton && <div className="circleIcon close" onClick={clear}><CloseIcon /></div>}
					{config.body && React.cloneElement(config.body)}
                </div>
			</div>
		)
};

export default Overlay;

export class OverlayConfig {
	constructor(body) {
		this.body = body
		this.className = null
        this.showCloseButton = false
	}
	setClassName = (cn) => {
		this.className = cn
		return this
	}
    showClose = () => {
		this.showCloseButton = true
		return this
	}
}