import {useQuery} from "@tanstack/react-query";
import {useState} from "react";
import {patientClient} from "../API/PatientClient";
import Table from "../../../Common/Table/Table";
import {formatSSN, Patient} from "../Models/Patient";
import {useNavigate, useSearchParams} from "react-router-dom";
import {useTranslation} from "react-i18next";
import {CloseIcon} from "../../../Common/Icon";
import {PatientRoutingPrefix} from "../Routing/PatientRouting";

export default function PatientList(props) {
    const {t} = useTranslation();
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();
    const [filters, setFilters] = useState({
        sortOrder: 'ASC', 
        sortBy: 'lastname',
        limit: 20,
        page: 0
    })
    // TODO: might interfere with state.. 
    filters.search = searchParams.get('search');

    const patientSearchQuery = useQuery({
            queryKey: ["patient.search", filters],
            queryFn: () => {
                return patientClient.searchPatients(filters)
            }
        },
    )
    
    const columns = [
        {key: 'id'},
        {key: 'firstname', title: t('Voornaam'), sortable: true},
        {key: 'lastname', title: t('Achternaam'), sortable: true},
        {key: 'ssn', title: t('Rijksregisternummer'), format: formatSSN},
        {key: 'mutuality', title: t('Mutualiteit')},
    ]

    const handleRowClick = (patient: Patient) => {
        navigate("/patients/" + patient.id)
    }
    const handleHeaderClick = (headerKey) => {
        if (filters.sortBy !==  headerKey) {
            setFilters({...filters, sortBy: headerKey, sortOrder: "ASC"});
        } else {
            if (filters.sortOrder === 'ASC') {
                setFilters({...filters, sortOrder: "DESC"});
            } else {
                setFilters({...filters, sortOrder: 'ASC', sortBy: 'lastname'});
            }
        }
    }

    const clearFilters = () => {
        setFilters({
            limit: 20,
            page: 0
        })
        if (searchParams.has('search')) {
            navigate("/"+PatientRoutingPrefix)
        }
    }
    return (
        <>
            <div className={"filters"}>
                {filters?.search?.length > 0 && <div>Searching: {filters.search} <CloseIcon onClick={clearFilters}/></div>}
            </div>
            {patientSearchQuery.isLoading && <div>Loading</div>}
            {patientSearchQuery.isSuccess && <Table
                data={patientSearchQuery.data}
                columns={columns}
                emptyMsg={t('Geen patienten')}
                sort={{
                    sortBy: filters.sortBy,
                    sortOrder: filters.sortOrder,
                    onSort: handleHeaderClick,
                }}
                onRowClick={handleRowClick}
            />}
        </>
    )
}