import './Styles/main.scss';
import './i18n';
import {QueryClient, QueryClientProvider} from "@tanstack/react-query";
import OverlayProvider from "./Components/Common/Overlay/OverlayProvider";
import NotificationProvider from "./Components/Common/Notification/NotificationProvider";
import AuthProvider from "./Components/Common/Auth/AuthProvider";
import AppRouter from "./Routing/AppRouter";


export default function App() {
  const queryClient = new QueryClient()
  return (
      <QueryClientProvider client={queryClient}>
          <OverlayProvider>
              <NotificationProvider>
                  <AuthProvider>
                      <AppRouter />
                  </AuthProvider>
              </NotificationProvider>
          </OverlayProvider>
      </QueryClientProvider>
  );
}
