import React, {useState, useContext} from "react";
import {OverlayConfig} from "./index";

const OverlayContext = React.createContext({
    config: null,
    activeOverlay: false,
    show: () => {},
    clear: () => {},
});

export const useOverlay = () => {
    return useContext(OverlayContext);
};

export default function OverlayProvider ({children}) {
    const [config, setConfig] = useState(null);
    const [activeOverlay, setActive] = useState(false);
    const show = (elm, showClose) => {
        let cfg = new OverlayConfig(elm)
        if (showClose) {
            cfg.showClose()
        }
        setConfig(cfg)
        setActive(true)
    }
    const clear = () => {
        setConfig(null)
        setActive(false)
    };
    return <OverlayContext.Provider
        value={{show, clear, activeOverlay, config}}
    >
        {children}
    </OverlayContext.Provider>
}