import axios from "axios";

function parseErrorResponse(error) {
	console.warn(error)
}

const parseJwt = (token) => {
	try {
		return JSON.parse(atob(token.split('.')[1]));
	} catch (e) {
		console.log(e, "parseJWT")
		return null;
	}
};

export const transformHeader = [function (data, headers) {
	headers.Authorization = `Bearer ${getCurrentUserToken()}`
	return data
}, ...axios.defaults.transformRequest]

export const authClient = axios.create({
	baseURL: process.env.REACT_APP_AUTH_API_HOST,
	transformRequest: transformHeader,
});

authClient.loginUser = function (email, pw) {
	return this.post("authenticate",{
		email: email,
		password: pw
	}).then(response => {
		if (response.data.BearerToken) {
			return response.data.BearerToken;
		}
	}, error => parseErrorResponse(error))
}

authClient.registerUser = function(email, password) {
	return this.post("register",{
		email: email,
		password: password
	}).then(response => {
		if (response.data.BearerToken) {
			return response.data.BearerToken;
		}
	}, error => parseErrorResponse(error))
}

authClient.logout = function() {
	localStorage.removeItem("userToken");
}

export function getCurrentUserToken() {
	const storedToken = localStorage.getItem("userToken")
	if (storedToken && storedToken.length > 0 && storedToken !== "undefined"){
		const decodedJWT = parseJwt(storedToken)
		if (decodedJWT && decodedJWT.exp * 1000 > Date.now()) {
			return storedToken
		}
		localStorage.removeItem("userToken");
	}
	return null
}

authClient.IsConnected = function() {
	const tkn = getCurrentUserToken()
	return tkn && tkn.length > 0;
}
