import {Outlet, Route, useParams} from "react-router-dom";
import {SectionPane} from "../../../Layout/SectionPane";
import PatientDetail from "../Components/PatientDetails";
import PatientInsurability from "../Components/PatientInsurability";
import PatientList from "../Components/PatientList";
import PatientInvoicing from "../Components/PatientInvoicing";
import {useQuery} from "@tanstack/react-query";
import {patientClient} from "../API/PatientClient";

export const PatientRoutingPrefix = "patients"

export const PatientProvider = (props) => {
    // Load the patient from params
    const params = useParams()
    const patientId = params?.patientId
    const patientQuery = useQuery({
            queryKey: ["patient.id", patientId],
            queryFn: () => {
                return patientClient.getPatient(patientId)
            }
        },
    )
    // use const { patient } = useOutletContext(); to fetch in child routes.
    if (patientQuery.isSuccess) {
        const patient = patientQuery.data
        return <Outlet context={{patient}} />
    }
    return ""
};

export const PatientRouting = <Route path={PatientRoutingPrefix} exact element={<SectionPane />}>
        <Route exact path={""} element={<PatientList />} />
        <Route exact path={":patientId"} element={<PatientProvider />} >
            <Route exact path={""} element={<PatientDetail />} />
            <Route exact path={"insurability"} element={<PatientInsurability />} />
            <Route exact path={"invoicing"} element={<PatientInvoicing />} />
        </Route>
    </Route>
 
