import useAuth from "../Components/Common/Auth/AuthProvider";
import {authClient} from "../Components/Common/Auth/client";
import {BrowserRouter, Outlet, Route, Routes} from "react-router-dom";

import PageNotFound from "./PageNotFound";
import {PatientRouting} from "../Components/Features/Patients/Routing/PatientRouting";
import Login from "../Components/Common/Auth/Login";

export const ProtectedRoute = () => {
    const {token} = useAuth();
    if (!token || !authClient.IsConnected()) {  // Check if the user is authenticated
        // If not authenticated, redirect to the login page
        console.log("Authentication redirect omitted.")
        //return <Login />;
    }
    // If authenticated, render the child routes
    return <Outlet />;
};


export default function AppRouter() {
    return (
        <BrowserRouter>
            <Routes>
                <Route key="protected" element={<ProtectedRoute/>}>
                    {PatientRouting}
                </Route>
                <Route key={"general-not-found"} path={"*"} element={<PageNotFound/>}/>
            </Routes>

        </BrowserRouter>
    )
}