import {useParams} from "react-router-dom";
import {useTranslation} from "react-i18next";
import SidebarSection, {SidebarLink, SidebarCollapsableMenu} from "../../../Layout/Sidebar/SidebarSection";
import {CalendarIcon, HeartIcon} from "../../../Common/Icon";

export default function PatientSidebarSection(props) {
    const { t, i18n } = useTranslation();
    let params = useParams();
    const patientId = params?.patientId
    
    return <SidebarSection title={t("Administratie")}>
        <SidebarLink to="/patients" end><HeartIcon small />{t("Patientenoverzicht")}</SidebarLink>
        {patientId &&<SidebarCollapsableMenu label={t("Patient dossier")}>
                    <SidebarLink to={"/patients/"+patientId} end><CalendarIcon small /> {t("Patient gegevens")}</SidebarLink>
                    <SidebarLink to={"/patients/"+patientId+"/insurability"} end>{t("Verzekerbaarheid")}</SidebarLink>
                    <SidebarLink to={"/patients/"+patientId+"/invoicing"} end>{t("Financiële instellingen")}</SidebarLink>
        </SidebarCollapsableMenu>}
        <SidebarLink to="/contactpersons" end>{t("Contactpersonen")}</SidebarLink>
        <SidebarLink to="/messages" end>{t("Meldingen")}</SidebarLink>
    </SidebarSection>
}

