import {NavLink} from "react-router-dom";
import {useState} from "react";

export function SidebarLink(props) {
    return <NavLink className="menu-item" {...props} ><div className={"navlink-title"}>{props.children}</div></NavLink>
}

export function SidebarCollapsableMenu(props) {
    const [showMenu, setShowMenu] = useState(true);
    return <>
        <div className="menu-section-title">{props.label}</div>
        {showMenu && <div className="menu-section">
            {props.children}
        </div>}
    </>
}

export default function SidebarSection(props) {
    return (
        <div className="sidebar-section">
            {props.title && <h3 className="section-title">{props.title}</h3>}
            {props.children}
        </div>
    )
}