import {Icon} from "@iconify/react";
import React from "react";
import "./icon.scss";


// Lookup icons : https://icon-sets.iconify.design/

export function Iconic (props) {
    const size = props.small ? "16" : "24"
    return <Icon width={size} height={size} {...props} className={props?.className + " iconic"}  />
}

export function ExitIcon( props) {
    return <Iconic {...props} icon="streamline:emergency-exit-solid" />
}

export function PlusIcon(props) {
   return <Iconic {...props}  icon="gg:add-r"/>
}

export function EditIcon(props) {
    return <Iconic {...props} className="action"  icon="bx:edit"/>
}

export function FilterIcon(props) {
    return <Iconic {...props}  icon="clarity:filter-line" />
}

export function CloseIcon(props) {
    return <Iconic {...props}  icon="gg:close-r" />
}
export function HeartIcon(props) {
    return <Iconic {...props}  icon="material-symbols:ecg-heart" />
}

export function SearchIcon(props) {
    return <Iconic {...props}  icon="garden:search-fill-16" />
}

export function CalendarIcon(props) {
    return <Iconic {...props}  icon="mdi:calendar-month" />
}
export function ArrowUpIcon(props) {
    return <Iconic {...props}  icon="material-symbols:arrow-drop-up" />
}
export function ArrowDownIcon(props) {
    return <Iconic {...props}  icon="material-symbols:arrow-drop-down" />
}