import './table.scss'
import {t} from "i18next";
import {ArrowDownIcon, ArrowUpIcon} from "../Icon";

export default function Table(props) {
    return <div className={"table-wrapper"}>
            <table className="table">
                {props.caption && <caption>
                    {props.caption}
                </caption>}
                <colgroup>
                    {props.columns.map((column) => {
                        return <col />
                    })}
                </colgroup>
                <thead className="table-header">
                <tr className="row">
                    {props.columns.map((column,idx) => {
                        return <ColumnHeadCell column={column} key={"th-"+idx} sort={props?.sort} />
                    })}
                </tr>
                </thead>
                <tbody className="table-body">
                {props.data.length === 0 && <tr className="row"><td colSpan={props.columns.length}>{props?.emptyMsg ?? t('Geen data')}</td></tr>}
                {props.data.map((rowData, idx) => {
                    return <tr key={"rw"+idx} onClick={() => props?.onRowClick(rowData)} className="row">
                        {props.columns.map((column,cidx) => {
                            return <td key={"td-"+idx+"-"+cidx} onClick={rowData[column?.onClick]}>{column.format ? column.format(rowData[column.key]) : rowData[column.key]}</td>
                        })}
                        {props.actions && props.actions.map((action, aidx) => {
                            return <td key={"td-"+aidx+"-action"} onClick={action.onClick}>{action.title}</td>
                        })}
                    </tr>
                })}
                </tbody>
            </table>
        </div>
    
}


function ColumnHeadCell(props) {
    const column = props.column;
    let sortIcon = ""
    if (props?.sort?.sortBy === column.key) {
        if (props.sort?.sortOrder === "ASC") {
            sortIcon = <ArrowUpIcon small />
        }else if (props.sort?.sortOrder === 'DESC') {
            sortIcon = <ArrowDownIcon small />
        }
    }
   
    return <th onClick={() => props.sort.onSort(column.key)}>{column.title} {sortIcon}</th>
}
