import axios from "axios";
import {transformHeader} from "../../../Common/Auth/client";
import {Patient} from "../Models/Patient";
import { setupCache } from 'axios-cache-interceptor';
import {dummyPatients} from "./dummyData";

const patientInternalClient = axios.create({
    baseURL: process.env.REACT_APP_AUTH_API_HOST,
    transformRequest: transformHeader,
});

export const patientClient = setupCache(patientInternalClient);



patientClient.searchPatients = function (filters): Patient[] {
    let patients  = dummyPatients;
    
    if (filters.search && filters.search.length > 0) {
        const searchTerm = removeNonAlphanumeric(filters.search.toUpperCase())
        patients = patients.filter((patient: Patient) => {
            return (patient.firstname.toUpperCase().indexOf(searchTerm) !== -1 || 
                patient.lastname.toUpperCase().indexOf(searchTerm) !== -1 ||
                patient.ssn.toUpperCase().indexOf(searchTerm) !== -1 )
            
        });
    }
    if (filters.sortBy) {
        patients.sort((a, b) => {
            const nameA = a[filters.sortBy].toUpperCase(); // ignore upper and lowercase
            const nameB = b[filters.sortBy].toUpperCase(); // ignore upper and lowercase
            if (nameA < nameB) {
                return filters.sortOrder === "ASC" ? -1 : 1;
            }
            if (nameA > nameB) {
                return filters.sortOrder === "ASC" ? 1 : -1;
            }
            // names must be equal
            return 0;
        });
    }
    return patients.slice(0, filters.limit)


    // return this.post("/patients/search", filters).then(response => {
    //     return response.data;
    // })
}

function removeNonAlphanumeric(str) {
    return str.replace(/[^a-zA-Z0-9]/g, '');
}

patientClient.getPatient = function (id): Patient {
    return dummyPatients.find((element) => element.id.toString() === id.toString());
}